canvas {

  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 1;
}

/*.homepage {
  text-align: center;
}*/

.tedx_logo {
  height: fit-content;
  width: 42vw;
  justify-content: center;
  z-index: 15;
  margin-bottom: 1.5rem;
  
}

.homepage {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  flex-direction: column;
}
.datedate{
display: flex;
justify-content: center;


}

.tedx_desc {
  margin-top: -15vh;
  max-width: 800px;
  padding: 20px;
  font-size: 20px;
}



/* theme-page */


/* custom scroll-bar */

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 0 gray;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb {
  background-color: #A9A9A9;
  border-radius: 5px;
}

@media only screen and (max-width: 1211px) {

  .canvas {
      position: absolute;
      height: 100%;
      width: 100%;
  }
  .homepage {
      text-align: center;
  }
  .tedx_logo {
      height: 15vh;
      width: 90%;
      align-items: left;
  }

}

/* CSS */
.buy_ticket {
background-color: transparent;
border: 1px solid #fd4c2d;
box-sizing: border-box;
color: #00132C;
font-family: "Avenir Next LT W01 Bold",sans-serif;
font-size: 16px;
font-weight: 700;
line-height: 24px;
padding: 16px 23px;
position: relative;
text-decoration: none;
user-select: none;
-webkit-user-select: none;
touch-action: manipulation;
width: fit-content;
margin: auto;
}

.buy_ticket:hover,
.buy_ticket:active {
outline: 0;
border: 1px solid #ffffff;
}

.buy_ticket:hover {
background-color: transparent;
cursor: pointer;
}

.buy_ticket:before {
background-color: #f6ddd5;
content: "";
height: calc(100% + 3px);
position: absolute;
right: -7px;
top: -9px;
transition: background-color 300ms ease-in;
width: 100%;
z-index: -1;
}

.buy_ticket:hover:before {
background-color: #fd4c2d;
}

@media (min-width: 768px) {

.buy_ticket {
  padding: 16px 32px;
}
}

.ticketReveal{
  margin-top: 5%;
}
.ticket_image{
  width: 400px;
}
.ticket_image{
  margin-top: 1rem;
  width: 60%;
  

  
}
@media only screen and (max-width: 768px) {
  .ticket_image {
    width: 95%;
  }
}